      // 设置游戏 URL 并切换显示状态
      this.gameUrl = this.initialGameUrl;
      this.isStart = true;
    },

    showPopup() {
      this.showMenuPop = true;
    },

    onClose() {
      this.showMenuPop = false;
    },
    clickCharacters() {
      this.showMenuPop = false;
      // 使用 Vue Router 导航到新页面，并传递 `item` 数据
      this.$router.push({
        path: "/character",
      });
    },
    clickDownload() {
      this.showMenuPop = false;
    },
    clickPhase() {
      this.showMenuPop = false;
    },
    navigateToPrivacy() {
      this.$router.push({ path: "/privacy" });
    },
    navigateToTerms() {
      this.$router.push({ path: "/terms" });
    },
  },
};
</script>
